/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

.btn,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 0px;
	border: none;
	color: $color__background-body;
	font-family: $font__secondary;
	line-height: 1;
	background: $color__inhabitent-green;	
	@include font-size(0.9);
	line-height: 1;
	padding: ($base__space-unit / 2) $base__space-unit;
	
	text-decoration: none;
	text-transform: uppercase;

	&:hover,
	&:active,
	&:focus {
		background: darken($color__inhabitent-green, 10%);
		color: white;
	}
}

.black-btn {
	@extend .btn;
	background-color: transparent;
	border: 1px solid $colar__inhabitent-bordercolor;
	color: black;
	
	&:hover,
	&:active,
	&:focus {
		background-color: $color__inhabitent-green;
		border: $color__inhabitent-green;
		color: $color__background-body;
}
}



/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	background-color: $color__background-screen;

	&:focus {
		color: #111;
	}
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
	width: 50%;
}

textarea {
	padding-left: 3px;
	width: 50%;
}

form label {
	text-transform: uppercase;
	font-family: $font__secondary; 
	margin-bottom: .5rem;
	letter-spacing: 1px;
}

.required {
	color: $color__inhabitent-primary;
	font-size: 1.1rem;
}